import React, { ReactElement } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";

import App from "./App";
import { APP_ROUTES } from "./constants/app";
import Home from "./pages/Home";
import Tools from "./pages/Tools";
import Crypto from "./pages/Tools/Crypto";

interface T_Props {}

export default function AppRoutes({}: T_Props): ReactElement {
  return (
    <BrowserRouter>
      <Routes>
        <Route path={APP_ROUTES.HOME} element={<App />}>
          <Route index element={<Home />} />
          <Route path={APP_ROUTES.TOOLS} element={<Tools />} />
          <Route path={APP_ROUTES.TOOLS_CRYPTO} element={<Crypto />} />
        </Route>
      </Routes>
    </BrowserRouter>
  );
}
