import React from "react";
import { Link, Outlet } from "react-router-dom";

import { APP_ROUTES } from "../../constants/app";
import { getLastPart } from "../../helpers/string";
import timeIcon from "../../images/calendar-time.svg";
import lockIcon from "../../images/lock.svg";
import "./styles.scss";

export default function Tools() {
  return (
    <div className="Tools">
      {getToolsListMarkup()}
      {/* Outlet for child routes */}
      <Outlet />
    </div>
  );
}

function getToolsListMarkup() {
  const markup: JSX.Element[] = [];

  toolsListMap.forEach((item, key) => {
    markup.push(
      <span key={item.name} className="tool">
        <Link to={key}>
          <img src={item.icon} alt={item.name} />
          <h2>{item.name}</h2>
        </Link>
      </span>
    );
  });

  return markup;
}

const toolsListMap = new Map([
  [
    APP_ROUTES.TOOLS_CRYPTO,
    {
      name: getLastPart(APP_ROUTES.TOOLS_CRYPTO, "/").toUpperCase(),
      icon: lockIcon
    }
  ],
  [
    APP_ROUTES.TOOLS_TIME,
    {
      name: getLastPart(APP_ROUTES.TOOLS_TIME, "/").toUpperCase(),
      icon: timeIcon
    }
  ]
]);
