import React, { useRef, useState } from "react";
import CryptoAES from "crypto-js/aes";
import CryptoUTF8Enc from "crypto-js/enc-utf8";

import "./styles.scss";

export default function Crypto() {
  const keyInputEl = useRef<HTMLInputElement>(null);
  const encryptInputEl = useRef<HTMLInputElement>(null);
  const decryptInputEl = useRef<HTMLInputElement>(null);
  const [cipherText, setCipherText] = useState("");
  const [originalText, setOriginalText] = useState("");

  const encrypt = () => {
    let encryptionResult: string;
    try {
      encryptionResult = CryptoAES.encrypt(
        encryptInputEl?.current?.value ?? "",
        keyInputEl?.current?.value ?? ""
      ).toString();
    } catch (error) {
      encryptionResult =
        (error as Error)?.message ?? "ERROR::Failed to encrypt";
    }
    setCipherText(encryptionResult);
  };

  const decrypt = () => {
    let decryptionResult: string;
    try {
      const bytes = CryptoAES.decrypt(
        decryptInputEl?.current?.value ?? "",
        keyInputEl?.current?.value ?? ""
      );
      decryptionResult = bytes.toString(CryptoUTF8Enc);
    } catch (error) {
      decryptionResult =
        (error as Error)?.message ?? "ERROR::Failed to decrypt";
    }
    setOriginalText(decryptionResult);
  };

  return (
    <div className="Global-centered Crypto">
      <div className="group">
        <input type="input" ref={keyInputEl} required />
        <span className="highlight"></span>
        <span className="bar"></span>
        <label>Secret Key</label>
      </div>
      <div className="group">
        <input type="input" ref={encryptInputEl} required />
        <span className="highlight"></span>
        <span className="bar"></span>
        <label>Original Message</label>
        <button onClick={encrypt}>encrypt</button>
        {cipherText && (
          <pre>
            <code>{cipherText}</code>
          </pre>
        )}
      </div>
      <div className="group">
        <input type="input" ref={decryptInputEl} required />
        <span className="highlight"></span>
        <span className="bar"></span>
        <label>Decrypted Message</label>
        <button onClick={decrypt}>decrypt</button>
        {originalText && (
          <pre>
            <code>{originalText}</code>
          </pre>
        )}
      </div>
    </div>
  );
}
